<template>
    <div class="v-container">
        <!-- 面包屑 -->
        <el-breadcrumb>
            <el-breadcrumb-item :to="{ name: 'setting' }">设置</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ name: 'sysManage' }">系统管理</el-breadcrumb-item>
            <el-breadcrumb-item>系统功能分配</el-breadcrumb-item>
        </el-breadcrumb>
        <hr>
        <el-row>
            <el-col :offset="2" :span="10">
                <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
                    <el-form-item label="选择角色" prop="role_id">
                        <el-select v-model="searchFormData.role_id" placeholder="角色名称" clearable>
                            <el-option v-for="item in role_code_list"
                                       :key="item._id"
                                       :label="item.role_name"
                                       :value="item._id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col :offset="2" :span="10">
                <el-button size="small" type="primary">应用权限</el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-col :offset="2" :span="8">
                <div :style="'height:'+getAutoHeight(178)+';overflow-y:scroll;background-color: #fff;padding: 10px;'">
                    <el-tree
                            show-checkbox
                            draggable
                            :data="treeData"
                            :props="defaultProps"
                            default-expand-all
                            @check="treeCheck"
                            :render-content="renderContent"
                            ref="tree_ref">
                    </el-tree>
                </div>
            </el-col>
            <el-col :span="4">
                <div :style="'text-align:center; width:100%;margin:0px;padding-top:'+ getAutoHeight(600)+';'">
                    <el-row>
                        <el-button v-if="(is_to_right && is_select_role_id)" type="primary" circle @click="addRouterAuthority"><i class="iconfont icon-xiangyou"></i></el-button>
                        <el-button v-else type="info" circle><i class="iconfont icon-xiangyou"></i></el-button>
                    </el-row>
                    <hr>
                    <el-row>
                        <el-button v-if="(is_to_left && is_select_role_id)" type="primary" circle @click="removeRouterAuthority"><i class="iconfont icon-xiangzuo"></i></el-button>
                        <el-button v-else type="info" circle><i class="iconfont icon-xiangzuo"></i></el-button>
                    </el-row>
                </div>
            </el-col>
            <el-col :span="8">
                <div :style="'height:'+getAutoHeight(178)+';overflow-y:scroll;background-color: #fff;padding: 10px;'">
                    <el-tree
                            show-checkbox
                            draggable
                            :data="addTreeData"
                            :props="defaultProps"
                            default-expand-all
                            :render-content="renderContent"
                            @check="addTreeCheck"
                            ref="tree_ref">
                    </el-tree>
                </div>
            </el-col>
        </el-row>
    </div>
</template>


<script>
    export default {
        name: 'sysFunctionConf',
        data() {
            return {
                role_code_list: null,
                searchFormData: {
                    role_id: null
                },
                is_select_role_id: false,
                is_to_right: false,
                is_to_left: false,
                treeData: [],
                treeCheckedNodes: [],
                treeHalfCheckedNodes: [],
                addTreeData: [],
                addTreeCheckedNodes: [],
                addTreeHalfCheckedNodes: [],
                defaultProps: {
                    children: 'children',
                    label: 'label'
                }
            }
        },
        watch: {
            'searchFormData.role_id': {
                handler(newName, oldName) {
                    if (newName !== null) {
                        this.is_select_role_id = true;
                        if (newName !== oldName) {
                            this.search_add_tree_data();
                        }
                    }
                },
                deep: true,
                immediate: true
            },
            treeCheckedNodes: {
                handler(n, o) {
                    this.is_to_right = (n.length > 0)
                }
            },
            addTreeCheckedNodes: {
                handler(n, o) {
                    this.is_to_left = (n.length > 0)
                }
            },
        },
        async mounted()/*查询系统角色*/ {
            let role_code_list_res = await this.$exec({'role_type_code_id': '1'}, 'OAMP_search_role_code_list');
            if (role_code_list_res != null && role_code_list_res.data != null)
                this.role_code_list = role_code_list_res.data;
            this.search_tree_data();
        },
        methods: {
            renderContent: function (h, {node, data, store}) {
                return h('span', [
                    h('i', {attrs: {class: 'iconfont ' + data.value.router_icon_class}}),
                    h('span', "     "),
                    h('span', node.label),
                ]);
            },
            async search_tree_data()/*查询原始权限树的数据*/ {
                let data = await this.$exec({}, 'OAMP_search_sys_router_tree_data');
                if (data === null) {
                    this.treeData = [];
                } else {
                    this.treeData = [data];
                }
            },
            async search_add_tree_data() /*查询权限树的数据*/ {
                let data = await this.$exec({'role_id': this.searchFormData.role_id}, 'OAMP_search_sys_router_tree_data');
                if (data === null) {
                    this.addTreeData = [];
                } else {
                    this.addTreeData = [data];
                }
            },
            treeCheck(val1, {checkedNodes, checkedKeys, halfCheckedNodes, halfCheckedKeys})/*勾选框check时*/ {
                this.treeCheckedKeys = checkedKeys;
                this.treeCheckedNodes = checkedNodes;
                this.treeHalfCheckedNodes = halfCheckedNodes;
            },
            addTreeCheck(val1, {checkedNodes, checkedKeys, halfCheckedNodes, halfCheckedKeys})/*勾选框check时*/ {
                this.addTreeCheckedNodes = checkedNodes;
                // this.addTreeHalfCheckedNodes = halfCheckedNodes;
            },
            async addRouterAuthority()/*添加路由权限*/ {
                let item = {data: [].concat(this.treeHalfCheckedNodes).concat(this.treeCheckedNodes), role_id: this.searchFormData.role_id};
                let data = await this.$exec(item, 'OMAP_add_router_authority');
                if (data !== null && data.is_success === 'true') {
                    this.$message.success('添加路由权限信息成功');
                    this.treeCheckedNodes = [];
                    this.search_tree_data();//去掉勾选状态
                    this.search_add_tree_data();
                }
            },
            async removeRouterAuthority()/*移除路由权限*/ {
                let item = {data: [].concat(this.addTreeCheckedNodes)};
                let data = await this.$exec(item, 'OMAP_remove_router_authority');
                if (data !== null && data.is_success === 'true') {
                    this.$message.success('移除路由权限信息成功');
                    this.addTreeCheckedNodes = [];
                    this.search_add_tree_data();
                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



